import { useEffect, useState } from 'react';
import { ARController } from 'artoolkit5-js'
import cameraData from '../static/camera_para.dat';
import trackMarker from '../utils/markerTracker';

const registerController = async () => {
  const controller = await ARController.initWithDimensions(640, 480, cameraData);
  const digitallsMarkerId = await controller.artoolkit.addNFTMarker(controller.id, 'ar-test/static/markers/digitalls_marker');
  trackMarker(controller, digitallsMarkerId);
}

const ViewerArToolkit = () => {
  useEffect(() => {
    registerController();
  })

  return <div>Hello</div>;
};

export default ViewerArToolkit;
